.whole-page .image-on-whole-screen {
  z-index: 1;
  width: 100%;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.9);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.whole-page .image-on-whole-screen .svg{
  position: absolute;
  top: 160px;
  margin-left: 420px;
}
.whole-page .image-on-whole-screen .svg:hover path {
  cursor: pointer;
  fill: orange;
}
.whole-page .image-on-whole-screen .image-pleace {
  width: 960px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.whole-page .image-on-whole-screen .image-pleace img {
  border-radius: 20px;
  opacity: 1 !important;
  width: 450px;
  position: relative;
}
.whole-page .image-on-whole-screen .image-pleace button {
  width: 50px;
  height: 50px;
  border: none;
  z-index: 1;
  margin: 0px -25px;
  border-radius: 50%;
  cursor: pointer;
}
.whole-page .image-on-whole-screen .image-pleace button:hover svg path {
  stroke: orange;
}
.flexed-imgs{
  margin-top: 50px;
  width: 450px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.flexed-imgs img{
  width: 80px;
  border-radius: 20px;
  cursor: pointer;
}
